<template>
    <div id="search">
       <Header :Flagadd=false :FlagHome=false :FlagCity=false :FlagSignin=false :FlagSearch=true :FlagOrder=false :FlagMy=false :FlagAcc=false>

        </Header>
      <div class="content">
          <div class="inp">
            <input type="text" placeholder="请输入商家或美食名称" class="inp1" v-model="inp" />
            <button class="btn" @click="ti()">提交</button>
          </div>
          <div class="con" :style="{'display':bSys?'block':'none'}" >
            <ul>
              <li>
                <div>{{this.msg}}</div>
              </li>
            </ul>
          </div>

          <h3>搜索历史</h3>
          <div class="con1" :style="{'display':bSys?'none':'block'}" >
            <ul>
              <li v-for="(item,index) in list" :key="index">
                <div>{{item}}<van-icon name="cross" class="van" @click="dell(index)" /></div>
              </li>
            </ul>
            <div class="del" @click="del()">清空所有</div> 
          </div>
          
      </div> 
    </div>
</template>

<script>
import Header from '../components/Header'
export default {
   components:{
     Header
    },
  name: 'search',
  data(){
    return{
      inp:'',
      msg:'抱歉没有结果',
      bSys:false,
      list:[]
    }
  },
  methods:{
    ti(){
      this.bSys=true;
      this.list.push(this.inp)
      for(var i=0;i<this.list.length;i++){
        for(var j=i+1;j<this.list.length;j++){
          if(this.list[i]==this.list[j]){
            this.list.splice(j,1);
            j--;
          }
        }
      }
      console.log(this.list)
      localStorage.setItem('SearchValue',JSON.stringify(this.list))
      this.axios.get(`http://elm.cangdu.org/v4/restaurants?extras[]=restaurant_activity&geohash=31.22299,121.36025&keyword=${this.inp}&type=search`).then(res=>{
      console.log(res.data)
    })
    },
    del(){
      this.list=[]
      localStorage.setItem('SearchValue',JSON.stringify(this.list))
    },
    dell(index){
      this.list.splice(index,1) 
      localStorage.setItem('SearchValue',JSON.stringify(this.list));
    }
  },
  mounted(){
    //读取本地存储的方法
    if(localStorage.getItem('SearchValue')){
      this.list=JSON.parse(localStorage.getItem('SearchValue'));
      console.log(this.list)
    }
    
  }
}
</script>

<style lang="scss">
#search{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    .content{
      width: 100%;
      flex: 1;
      margin-top: 2rem;
      .inp{
        width: 100%;
        height: 2rem;
        background: white;
        .inp1{
          margin-left: 5%;
          width: 65%;
          border: none;
          border: 0.01rem solid grey;
          border-radius: 0.1rem;
          height: 1.2rem;
          outline-style: none ;
          margin-top: 0.3rem;
          background: #f2f2f2;
          padding-left: 0.2rem;
        }
        .btn{
          margin-left: 0.2rem;
          width: 20%;
          background: #3190e8;
          color: white;
          border: none;
          border: 0.01rem solid grey;
          border-radius: 0.1rem;
          height: 1.3rem;
          outline-style: none ;
        }
      }
      .con{
        margin-top: 0.2rem;
        width: 100%;
        background: white;
        ul{
          li{
            line-height: 2rem;
            text-align: center;
          }
        }
      }
      h3{
        margin-top: 0.5rem;
        margin-left: 5%;
      }
      .con1{
        margin-top: 0.3rem;
        width: 100%;
        // padding-left: 5%;
        background: white;
        ul{
          width: 100%;
         
          li{
             line-height: 2rem;
              border-bottom: 0.01rem solid gainsboro;
              padding-left: 5%;
              div{
                position: relative;
                .van{
                  position: absolute;
                  right: 1rem;
                  top:0.8rem;
                }
              }
          }
        }
        .del{
                background: white;
                line-height: 2rem;
                
                text-align:center;
                color: #3190e8;
            }
      }
    }
}
    
</style>


